@charset "UTF-8";
/* A Modern CSS Reset */
/* https://coliss.com/articles/build-websites/operation/css/css-reset-for-modern-browser.html */
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
button,
input,
select,
figure,
blockquote,
div,
dd,
dl,
dt,
table,
td,
th {
  margin: 0;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  color: #000;
}

a {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

small {
  font-size: 10px;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

button {
  background: initial;
  cursor: pointer;
}

table {
  width: 100%;
  border-spacing: 0;
}

th, td {
  padding: 0;
}

address {
  font-style: normal;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
